import { createWebHashHistory, createRouter } from 'vue-router'

import MainView from '@/views/MainVue.vue'
import ReactNative from '@/views/ReactNativeView.vue'
import UnityView from '@/views/UnityView.vue'
import EmbeddedView from '../views/EmbeddedView.vue'
import AboutView from '../views/AboutView.vue'
import WebView from '../views/WebView.vue'

const routes = {
  Main: { path: '/', component: MainView, },
  About: { path: '/about', component: AboutView, },
  Vue: { path: '/web', component: WebView, },
  ReactNative: { path: '/react-native', component: ReactNative, },
  Unity: { path: '/unity', component: UnityView, },
  Embedded: { path: '/embedded', component: EmbeddedView, },
}

const router = createRouter({
  history: createWebHashHistory(),
  routes: Object.values(routes),
  scrollBehavior() {
    return { top: 0 }
  },
})

export default router;
export { routes as Routes };