<template>
  <div class="fade" aria-hidden="true"></div>
  <main class="main">
    <div class="main-grid">
      <RouterLink basics-heading basics-text basics-icon-header nav-home to="/" v-if="!isMain"><ArrowRight basics-header-icon flip-y />Main</RouterLink>
      <RouterView />
    </div>
  </main>
  <footer class="footer">
    <div basics-footer footer-flex>
      <p basics-info>This website does not use any cookies</p>
    </div>
    <div basics-footer footer-flex>
      <p basics-info>2024 Dmytro Hranchak</p>
      <p basics-info>Edited: <span ref="lastEdit"></span></p>
      <p basics-info>
        React me at
        <a basics-link decorated :href="generateMailtoLink()">{{email}}</a>
      </p>
    </div>
  </footer>
</template>

<script lang="ts">
  import { defineComponent } from 'vue';
  import { Icon } from '@iconify/vue';
  import ColorUtils from '@/utils/ColorUtils'
  import { ArrowRight } from '@/components/icons'
  import { Routes } from './router/router';
  import MailUtils from '@/utils/MailUtils';

  export default defineComponent({
    name: 'App',
    components: {
      ArrowRight
    },
    data() {
      return {
        isMain: true,
        theme: {
          lastIndex: 363,
          index: 0
        },
        colorRules: {
          darkenFactor: 30,
          decorationDarkenFactor: 50
        },
        email: MailUtils.email,
        colorScheme: {
          "name": "SeaShells",
          "black": "#17384c",
          "red": "#d15123",
          "green": "#027c9b",
          "yellow": "#fca02f",
          "blue": "#1e4950",
          "purple": "#68d4f1",
          "cyan": "#50a3b5",
          "white": "#deb88d",
          "brightBlack": "#434b53",
          "brightRed": "#d48678",
          "brightGreen": "#628d98",
          "brightYellow": "#fdd39f",
          "brightBlue": "#1bbcdd",
          "brightPurple": "#bbe3ee",
          "brightCyan": "#87acb4",
          "brightWhite": "#fee4ce",
          "background": "#09141b",
          "foreground": "#deb88d",
          "selectionBackground": "#1e4962",
          "cursorColor": "#fca02f"
        } as { [key: string]: string }
      };
    },
    mounted() {
      this.setTheme({ ...this.colorScheme });
      this.setLastModified();
    },
    watch: {
      $route(to, from) {
        this.isMain = to.path === Routes.Main.path;
      }
    },
    methods: {
      setTheme(themeJson: {[key:string]: string}) {
        this.colorScheme = themeJson;
        const root = document.documentElement;
        const dimColor = ColorUtils.darkenHex(this.colorScheme.white, this.colorRules.darkenFactor);
        const decorationColor = ColorUtils.darkenHex(this.colorScheme.white, this.colorRules.decorationDarkenFactor);

        root.style.setProperty('--background', this.colorScheme.background);
        root.style.setProperty('--text-color', this.colorScheme.white);
        root.style.setProperty('--text-color-dim', dimColor);
        root.style.setProperty('--decoration-color', decorationColor);
        root.style.setProperty('--decoration-active-color', dimColor);
        root.style.setProperty('--scrollbar', dimColor);

        /*Sphere*/
        root.style.setProperty('--web', this.colorScheme.red);
        root.style.setProperty('--common', this.colorScheme.white);
        root.style.setProperty('--embedded', this.colorScheme.yellow);
      },
      generateMailtoLink() {
        return MailUtils.generateMailtoLink();
      },
      changeTheme(direction: 1|-1) {
        this.theme.index += direction;
        if (this.theme.index < 0) {
          this.theme.index = this.theme.lastIndex;
        }
        if (this.theme.index > this.theme.lastIndex) {
          this.theme.index = 0;
        }
        fetch(`themes/${this.theme.index}.json`)
          .then(response => response.json())
          .then(data => {
            this.setTheme(data);
          })
      },
      setLastModified(): void {
        const lastModified: string = document.lastModified;
        const date: Date = new Date(lastModified);
        const options: Intl.DateTimeFormatOptions = { year: 'numeric', month: 'long', day: 'numeric' };
        const formattedDate: string = date.toLocaleDateString('en-US', options);

        const lastEdit = this.$refs.lastEdit as HTMLParagraphElement;
        if (lastEdit) {
          lastEdit.textContent = formattedDate;
        }
      }
    }
  });
</script>

<style>
  @import './styles/styles.css';
</style>
