<template>
  <div class="overlay">
    <div v-for="(node, index) in data"
         :key="index"
         class="chipContainer"
         :style="getComputedStyle(index)">
      <div></div>
      <p>
        {{ node.text }}
      </p>
    </div>
    <div ref="scene" scene></div>
  </div>
</template>

<script lang="ts">
  import { defineComponent, Ref, getCurrentInstance } from 'vue';
  import * as THREE from 'three';
  import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
  import { LineGeometry } from 'three/examples/jsm/lines/LineGeometry';
  import { LineMaterial } from 'three/examples/jsm/lines/LineMaterial';

  interface SphereEntry {
    text: string;
    coords: [number, number];
    depth: number;
    color?: string;
  }

  export default defineComponent({
    name: 'TechSphereComp',
    components: {
    },
    props: {
      color: String,
    },
    watch: {
    },
    data() {
      return {
        chipWidth: 100,
        radius: 2,
        camDistance: 3.5,
        autoRotateSpeed: 2,
        textFactor: {
          min: 1.5,
          max: 3,
        },
        chips: {
          web: ["Typescript", "Vue", "React Native", "Node.js", "Express.js", "Three.js", "Websockets", "AR", "Metalitix"],
          embedded: ["CAN", "ISO-TP", "LoRa", "C", "BLE", "STM32", "FreeRTOS", "SAE J1939"],
          common: ["Shaders", "C#", "Unity", "FFT", "Git", "Docker", "Scrum"],
        },
        data: [] as Array<SphereEntry>,
      };
    },
    computed: {
      getComputedStyle() {
        return (index: number) => {
          const entry = this.data[index];
          if (!entry) return {};

          const divRef = this.$refs.scene as HTMLDivElement;
          const containerSize = divRef.clientWidth;

          let maxSize = 40;
          if (containerSize <= 500) {
            maxSize = 30
          } 
          if (containerSize <= 400) {
            maxSize = 20
          } 

          const top = `${entry.coords[1]}px`;
          const left = `${entry.coords[0]}px`;
          const depthAdjustment = this.camDistance * this.textFactor.min - entry.depth;
          const fontSize = Math.max(5, Math.min(depthAdjustment * this.camDistance * this.textFactor.max, maxSize));

          return {
            top,
            left,
            zIndex: Math.round(fontSize),
            fontSize: `${fontSize}px`,
            color: entry.color,
            position: 'absolute',
          };
        };
      },
    },
    mounted() {
      this.createChips();
      const root = document.documentElement;
      const fontColor = root.style.getPropertyValue('--text-color');
      const divRef = this.$refs.scene as HTMLDivElement;
      if (divRef) {
        this.initScene(divRef, fontColor);
      }
    },
    methods: {
      createChips() {
        const arr: Array<SphereEntry> = [];
        const root = document.documentElement;

        for (const [key, category] of Object.entries(this.chips)) {
          const color = root.style.getPropertyValue(`--${key}`);
          const temp: string[] = [...category];
          temp.forEach((word, index) => {
            arr.push({ text: word, coords: [0, 0], depth: 0, color });
          });
        }
        this.shuffle(arr);
        this.data = arr;
      },
      shuffle(array: Array<any>) {
        let currentIndex = array.length;

        while (currentIndex != 0) {

          let randomIndex = Math.floor(Math.random() * currentIndex);
          currentIndex--;

          [array[currentIndex], array[randomIndex]] = [
            array[randomIndex], array[currentIndex]];
        }
      },
      async initScene(parent: HTMLDivElement, fontColor: string) {
        const scene = new THREE.Scene();
        const camera = new THREE.PerspectiveCamera(75, parent.offsetWidth / parent.offsetHeight, 0.1, 1000);
        camera.position.z = 3.5;
        const renderer = new THREE.WebGLRenderer({ antialias: false, alpha: true });
        renderer.outputColorSpace = THREE.LinearDisplayP3ColorSpace;

        const nodes: Array<THREE.Object3D> = [];

        parent.appendChild(renderer.domElement);

        const setupOrbitControl = (): OrbitControls => {
          const controls = new OrbitControls(camera, renderer.domElement);
          controls.enableDamping = true;
          controls.enableZoom = false;
          controls.enablePan = false;
          controls.dampingFactor = 0.1;
          controls.autoRotateSpeed = this.autoRotateSpeed;
          controls.autoRotate = true;
          controls.screenSpacePanning = false;
          return controls
        }

        const controls = setupOrbitControl();

        const instantiateNodes = async () => {
          const stringCount = this.data.length;
          this.data.forEach((item, i) => {
            const phi = Math.acos(-1 + (2 * i) / stringCount);
            const theta = Math.sqrt(stringCount * Math.PI) * phi;

            const x = this.radius * Math.sin(phi) * Math.cos(theta);
            const y = this.radius * Math.sin(phi) * Math.sin(theta);
            const z = this.radius * Math.cos(phi);

            const node = new THREE.Object3D();

            node.position.set(x, y, z);


            scene.add(node);
            nodes.push(node);
          });
        };

        const updateOverlayPosition = () => {
          nodes.forEach((node, index) => {
            const vector = node.position.clone();
            vector.project(camera);

            const widthHalf = parent.clientWidth / 2;
            const heightHalf = parent.clientHeight / 2;

            const screenX = (vector.x * widthHalf) + widthHalf;
            const screenY = -(vector.y * heightHalf) + heightHalf;

            this.data[index].coords = [Math.round(screenX * 100) / 100, Math.round(screenY * 100) / 100];
            this.data[index].depth = Math.round(camera.position.distanceTo(node.position) * 100) / 100;
          })
        };


        const animate = () => {
          requestAnimationFrame(animate);
          controls.update();
          updateOverlayPosition();
          renderer.render(scene, camera);
        };
        instantiateNodes();
        animate();
      },
    }
  });
</script>

<style>
  .overlay {
    width:100%;
    user-select: none;
    position: relative;
  }

  .chipContainer {
    position: absolute;
    transform: translate(-50%, -50%);
  }

    .chipContainer > div {
      width: 100%;
      height: 100%;
      backdrop-filter: blur(5px);
      mask-image: radial-gradient(ellipse closest-side, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
      -webkit-mask-image: radial-gradient(ellipse closest-side, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: -1;
    }

    .chipContainer p {
      white-space: nowrap;
    }

  [scene], [scene] * {
    width: 100%;
    height: 100%;
  }

  

</style>
